"use client";
import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { 
  BarChart2, 
  ShoppingBag, 
  Users, 
  DollarSign, 
  Settings, 
  LogOut, 
  ShieldAlert, 
  Bell, 
  Briefcase,
  Package
} from "lucide-react";
import { Montserrat } from "next/font/google";

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const montserrat = Montserrat({
    subsets: ["latin"],
    weight: "800",
    display: "swap",
  });
  
  const pathname = usePathname();
  const [isCollapsed, setIsCollapsed] = useState(false);
  
  // Information de l'administrateur (à remplacer par des données réelles)
  const adminInfo = {
    name: "Admin",
    email: "admin@example.com",
    photoUrl: null // null pour utiliser l'icône par défaut, sinon mettre l'URL de l'image
  };
  
  const navItems = [
    { name: "Tableau de bord", href: "/admin/dashboard", icon: BarChart2 },
    { name: "Gestion", href: "/admin/gestion", icon: Briefcase },
    { name: "Boutique", href: "/admin/boutique", icon: ShoppingBag },
    { name: "Ventes", href: "/admin/ventes", icon: DollarSign },
    { name: "Commandes", href: "/admin/commandes", icon: Package },
    { name: "Signalements", href: "/admin/signalements", icon: ShieldAlert },
    { name: "Notifications", href: "/admin/notifications", icon: Bell },
    { name: "Paramètres", href: "/admin/parametres", icon: Settings },
  ];
  
  return (
    <>
      <div
        className={`fixed top-0 left-0 h-screen ${isCollapsed ? "w-20" : "w-64"} bg-white transition-all duration-300 flex flex-col shadow-lg z-50 border-r border-gray-200`}
      >
        {/* Profil Admin */}
        <div
          className={`p-4 flex ${isCollapsed ? "flex-col" : "flex-col"} items-center border-b border-gray-200 pb-6`}
        >
          <div className="w-14 h-14 rounded-full bg-gray-200 flex items-center justify-center overflow-hidden mb-2">
            {adminInfo.photoUrl ? (
              <img src={adminInfo.photoUrl} alt="Admin" className="w-full h-full object-cover" />
            ) : (
              <Users className="h-8 w-8 text-gray-600" />
            )}
          </div>
          
          {!isCollapsed && (
            <>
              <h3 className={`${montserrat.className} text-base font-bold text-gray-900 mt-2`}>
                {adminInfo.name}
              </h3>
              <p className="text-xs text-gray-500 mt-1">{adminInfo.email}</p>
            </>
          )}
        </div>

        {/* Navigation */}
        <nav className="flex-1 py-4 overflow-y-auto">
          {navItems.map((item) => {
            const isActive = pathname === item.href;
            return (
              <Link
                key={item.name}
                href={item.href}
                className={`flex items-center gap-4 py-3 px-6 ${
                  isActive
                    ? "bg-blue-50 text-blue-600 border-r-4 border-blue-500"
                    : "text-gray-700 hover:bg-gray-50"
                } transition-all duration-200`}
              >
                <item.icon
                  className={`h-5 w-5 ${isActive ? "text-blue-500" : "text-gray-500"}`}
                />
                {!isCollapsed && <span>{item.name}</span>}
              </Link>
            );
          })}
        </nav>

        {/* Bouton de déconnexion */}
        <div className="p-4 border-t border-gray-200">
          <button
            className="flex items-center gap-4 py-3 px-6 w-full text-gray-700 hover:bg-red-50 hover:text-red-600 transition-all duration-200 rounded-lg"
          >
            <LogOut className="h-5 w-5" />
            {!isCollapsed && <span>Déconnexion</span>}
          </button>
        </div>

        {/* Bouton pour collapse/expand */}
        <button
          onClick={() => setIsCollapsed(!isCollapsed)}
          className="absolute top-4 right-[-12px] p-1 rounded-full border bg-white text-gray-600 border-gray-300 shadow-sm"
        >
          {isCollapsed ? ">" : "<"}
        </button>
      </div>
      
      {/* Contenu principal avec un padding pour éviter que le contenu passe sous la sidebar */}
      <main className={`${isCollapsed ? "ml-20" : "ml-64"} transition-all duration-300 min-h-screen bg-gray-50 p-6`}>
        {children}
      </main>
    </>
  );
}